import { createSlice } from "@reduxjs/toolkit"


//   export 
export const HomeSilce = createSlice({
  name: 'screen',
  initialState: {
    fullscreen: true
  },
  reducers: {
    setscreen: (state ) => {state.fullscreen = !state.fullscreen},
  
  }
})
// actions 
export const { setscreen } = HomeSilce.actions
